import './App.css';
import axios from 'axios';
import * as dd from 'dingtalk-jsapi';

//内网穿透工具介绍:
// https://developers.dingtalk.com/document/resourcedownload/http-intranet-penetration?pnamespace=app
// 替换成后端服务域名
export const domain = "";

function App() {

    dd.ready(function () {
        // let corpId;
        // axios.get(domain + "/config")
        //     .then(response => {
        //         corpId = response.data.corpId;
        //     })
        //     .catch(error => {
        //         alert(JSON.stringify(error))
        //         // console.log(error.message)
        //     })
        let corpId, agentId;
        fetch(domain + '/config')
            .then(res => res.json())
            .then((result) => {
                // alert(JSON.stringify(result));
                corpId = result.result.corpId;
                agentId = result.result.agentId;
                // dd.ready参数为回调函数，在环境准备就绪时触发，jsapi的调用需要保证在该回调函数触发后调用，否则无效。
                dd.runtime.permission.requestAuthCode({

                    corpId: corpId, //三方企业ID
                    onSuccess: function (result) {
                        // alert(JSON.stringify(result));
                        axios.get(domain + "/login?authCode=" + result.code)
                            .then(response => {
                                // alert(JSON.stringify(response));
                                // alert(JSON.stringify(response.data));
                                // alert(JSON.stringify(response.data.result.userid));
                                // alert(JSON.stringify(response.data.result.deptIdList[0]));
                                // 登录成功后储存用户部门和ID
                                sessionStorage.setItem("userId", response.data.result.userid);
                                sessionStorage.setItem("unionId", response.data.result.unionid);
                                sessionStorage.setItem("deptId", response.data.result.deptIdList[0]);
                                sessionStorage.setItem("corpId", corpId);
                                sessionStorage.setItem("agentId", agentId);
                                // const qs = require('qs');
                                // axios.get(domain + "/users", {
                                //     params: {
                                //         deptIds: response.data.result.deptIdList,
                                //     },
                                //     paramsSerializer: function (params) {
                                //         return qs.stringify(params, {arrayFormat: 'repeat'});
                                //     }
                                // }).then(response => {
                                //     console.log(response);
                                //     // 此处为硬编码邀请人的unionid，可以使用response里面返回的数据
                                //     sessionStorage.setItem("inviteUnionId", "***");
                                // }).catch(error => {
                                //     alert(JSON.stringify(error));
                                // })
                            })
                            .catch(error => {
                                alert(JSON.stringify(error));
                                // console.log(error.message)
                            })

                    },
                    onFail: function (err) {
                        alert(JSON.stringify(err))
                    }
                });
            });
    });

    const createMeeting = () => {

        dd.biz.contact.complexPicker({
            title: "测试标题",            //标题
            corpId: sessionStorage.getItem("corpId"),              //企业的corpId
            multiple: true,            //是否多选
            limitTips: "超出了",          //超过限定人数返回提示
            maxUsers: 1000,            //最大可选人数
            pickedUsers: [],            //已选用户
            pickedDepartments: [],          //已选部门
            disabledUsers: [],            //不可选用户
            disabledDepartments: [],        //不可选部门
            requiredUsers: [],            //必选用户（不可取消选中状态）
            requiredDepartments: [],        //必选部门（不可取消选中状态）
            appId: sessionStorage.getItem("agentId"),              //微应用Id，企业内部应用查看AgentId
            // permissionType: "xxx",          //可添加权限校验，选人权限，目前只有GLOBAL这个参数
            responseUserOnly: false,        //返回人，或者返回人和部门
            startWithDepartmentId: 0,   //仅支持0和-1
            onSuccess: function (result) {
                alert(JSON.stringify(result))
                let deptIds = result.result.departments;
                let userIds = result.result.users.map(user => user.emplId);

                // 获取存储的用户部门和ID
                const userId = sessionStorage.getItem('userId');
                const unionId = sessionStorage.getItem('unionId');
                const deptId = sessionStorage.getItem('deptId');
                // demo直接构建了要请求的数据，实际开发需要从页面获取
                const data = {
                    "userId": unionId,
                    "confTitle": "会议Demo",
                    "inviteDeptIds": deptIds,
                    "inviteUserIds": userIds
                };
                // 发起会议
                axios({
                    url: domain + '/meeting',
                    method: 'post',
                    data: data,
                    headers: {
                        'Content-Type': 'application/json'
                    }
                })
                    .then(function (response) {
                        // alert(JSON.stringify(response));
                        console.log(response);
                        sessionStorage.setItem("conferenceId", response.data.result.conferenceId);

                    })
                    .catch(function (error) {
                        console.log(error);
                    });

            },
            onFail: function (err) {
                alert(JSON.stringify(err));
                console.log(JSON.stringify(err));
            }
        });

    };

    const closeMeeting = () => {
        // 获取存储的用户部门和ID
        const unionId = sessionStorage.getItem('unionId');
        const conferenceId = sessionStorage.getItem('conferenceId');

        // 关闭会议
        axios({
            url: domain + '/meeting?conferenceId=' + conferenceId + '&unionId=' + unionId,
            method: 'put',
            headers: {
                'Content-Type': 'application/json'
            }
        })
            .then(function (response) {

            })
            .catch(function (error) {
                console.log(error);
            });
    };

    return (
        <div className="App">
            {/*<header className="App-header">*/}
            {/*<button onClick={createMeeting}>领用并提交审批</button>*/}
            {/*</header>*/}
            <header className="App-header">
                <button onClick={createMeeting}>发起会议</button>
                <button onClick={closeMeeting}>关闭会议</button>
            </header>
            {/*<div className="container">*/}
            {/*    <List/>*/}
            {/*</div>*/}
        </div>
    );
};


export default App;
